.head{
    width: 100%;
    height: 240px;
    padding-top: 21px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    text-align: center;
    background-image: linear-gradient(0deg,#fff,hsla(0,0%,93.3%,.7)),url(https://images.unsplash.com/photo-1466637574441-749b8f19452f?ixid=MXwxMjA3fDB8MHxzZWFyY2h8MzB8fGZvb2R8ZW58MHx8MHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=800&q=60);
    border-bottom: 1px solid rgb(184, 182, 182);
}
.title{
    color: orange;
    font-size: 3rem;
    font-family: serif;
    line-height: 5;
}
.content{
    margin: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.img{
    height: 500px;
    width: 50%;
    box-shadow: 2px 2px 6px 3px  rgba(0,0,0,0.3);
}
ul{
    list-style: none;
}
ul li::before {
    content: "\2022";  
    color: rgb(240, 132, 44);
    font-weight: bold;
    display: inline-block; 
    width: 1em; 
    margin-left: -1em;
  }
.names{
    font-family: serif;
    font-size: 1.3rem;
    color:  rgb(90, 108, 112);
}