.head{
    width: 100%;
    height: 240px;
    padding-top: 21px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50%;
    text-align: center;
    background-image: linear-gradient(0deg,#fff,hsla(0,0%,93.3%,.7)),url(https://images.unsplash.com/photo-1466637574441-749b8f19452f?ixid=MXwxMjA3fDB8MHxzZWFyY2h8MzB8fGZvb2R8ZW58MHx8MHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=800&q=60);
    border-bottom: 1px solid rgb(184, 182, 182);
    display:flex;
    flex-direction: column;
}
.title{

    color: orange;
    align-self: center;
    margin-top: 50px;
    margin-bottom: 0px;
    font-size: 3rem;
    font-family: serif;
}
.explaination{
    font-size: 1rem;
    align-self: center;
    width: 700px;
    font-family: serif;
    color: slategray;
}
.Cuisine{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.type1{
    color: white;
    margin: 70px;
    height: 150px;
    width: 250px;
    border: 2px solid;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 20px;
    background: url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1607757497335&di=be6d85e17dc8006bc69adb2795b4238f&imgtype=0&src=http%3A%2F%2Fn.sinaimg.cn%2Fsinacn13%2F319%2Fw700h419%2F20180803%2F1d0d-hhehtqh3647998.png");
    background-position: center;
    background-size: cover;
    box-shadow: 2px 2px 6px 3px  rgba(0,0,0,0.3);
    font-family: serif;
    font-size: 1.2rem;

}
.type1:hover{
    background: linear-gradient(orange,transparent),
    url('https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1607757497335&di=be6d85e17dc8006bc69adb2795b4238f&imgtype=0&src=http%3A%2F%2Fn.sinaimg.cn%2Fsinacn13%2F319%2Fw700h419%2F20180803%2F1d0d-hhehtqh3647998.png');
    background-position: center;
    background-size: cover;
}
